* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Comic Sans MS", "Comic Sans";
    background: #f1f5f8;
    color: #222;
}

.booklist {
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}

#bookCover {
    width: 180px;
}

.book {
    text-align: center;
}

@media screen and (min-width: 768px) {
    .booklist {
        grid-template-columns: repeat(3, 1fr);
        /* align-items: start;  /*This will only extend the grid to only where element exits
       / */
    }
}

.book {
    background: cornsilk;
    border-radius: 1rem;
    padding: 1rem 2rem;
}

.book h1 {
    margin-top: 0.5cm;
    font-size: 20px;
    font-weight: bold;
}

.book h2 {
    margin-top: 0.5rem;
    font-size: 15px;
    font-weight: 700;
}

.book p {
    margin-top: 0.5rem;
}

/* Temporary */
#link {
    text-decoration: none;
}
